import {mapState} from 'vuex';
import uniIconfont from '@/components/uni-iconfont/uni-icon.vue'
import uniIcon from '@/components/uni-icon/uni-icon.vue';
import goodList from '@/components/good-list/good-list.vue';
import {turnPage} from '@/common/global.js';
import {ajaxGetAddresses, ajaxPlaceOrder} from '@/data/ajax.js';
export default {
	components: {
		uniIcon,
		uniIconfont,
		goodList
	},
	data() {
		return {
			selectAll: false
		};
	},
	computed: {
		...mapState(['car', 'consignees']),
		carData: {
			get () {
				return this.$store.getters.doneCar;
			},
			set () {
				// 
			}
		},
		selectResult () {
			let result = this.$store.getters.doneSelectResult;
			this.selectAll = result.allCount === result.selectCount;
			return result;
		}
	},
	onLoad() {
		let _this = this;
		// 获取收货地址信息
		if (this.consignees.length === 0) {
			ajaxGetAddresses(this.$store.state.user, (data) => {
				_this.$store.commit('setConsignee', data.result);
			});
		}
	},
	methods: {
		clkQgg () {
			uni.navigateBack();
		},
		clkSelectAll () {
			this.selectAll = !this.selectAll;
			this.$store.commit('setSelectAll', [this.selectAll]);
		},
		clkConsignee () {
			turnPage('consignee', 'car');
		},
		changeCount (data) {
			console.log('===changeCount===');
			console.log(data);
			this.EVENTHUB.$emit('updateCount', data);
		},
		clkNext () {
			let selectGoods = this.selectResult.selectGoods;
			let consigneesId = this.consignees && this.consignees[0] && this.consignees[0]._id;
			
			if (!selectGoods || selectGoods.length === 0) {
				uni.showToast({title: '请先选择商品', icon: 'none', position: 'bottom'});
			} else if (!consigneesId) {
				uni.showToast({title: '请选择配送地址', icon: 'none', position: 'bottom'});
			} else {
				turnPage('car-sub');
			}
		},
		clkPlaceOrder () {
			let goods = [];
			// let obj = Object.assign(this.consignees[0] || {}, {
			// 	goods: this.selectResult.selectGoods,
			// 	money: this.selectResult.selectMoney,
			// 	count: this.selectResult.selectCount
			// });
			this.selectResult.selectGoods.forEach(function (item) {
				goods.push({
					_id: item._id,
					specsId: item.specsInfo._id,
					count: item.count
				});
			});
			console.log('===clkPlaceOrder===');
			console.log(this.selectResult.selectGoods);
			ajaxPlaceOrder({goods: goods, consigneesId: this.consignees[0] && this.consignees[0]._id}, () => {
				let _this = this;
				uni.showToast({'title': '预定成功'});
				setTimeout(() => {
					turnPage('order', 1);
				}, 1000);
				setTimeout(() => {
					// 删除购物车中已购买的商品
					_this.selectResult.selectGoods.forEach(function (item) {
						item.count = 0;
						_this.EVENTHUB.$emit('updateCount', item);
					});
					_this.$store.commit('delGoods');
				}, 1500);
			});
		}
	}
};
